.header{
    display: flex;
    justify-content: space-between;
    border: 1px solid;
    background-color: gainsboro;
}
.logo{
    width:110px;
}
.nav-items > ul {
    display: flex;
    list-style-type: none;
    font-weight: 1000;
    font-size: 20px;
    
}
.restaurant-container{
    flex-wrap: wrap;
    display: flex;
}
.nav-items > ul > li{
    padding: 20px;
}
.res-card {
    width: 180px;
    padding: 5px;
    margin: 5px;
}
.res-card:hover{
    border: 1px solid black;
    cursor: pointer;
}
.res-logo{
    width: 100%;
    height: 130px;
    
   
}